import React from 'react';
import { Navbar,Container,Nav } from 'react-bootstrap';
import './NavBoot.css';
import Menu from './menu.png';
import "animate.css";
import { Link } from 'react-scroll';
import Hari from './harilogo.jpg';
import { useState } from 'react';


const NavBoot = ()=>{ 
  window.addEventListener("scroll", function(){
    var header = this.document.querySelector("header");
    header.classList.toggle("sticky",window.scrollY > 0);
  })
  const [expanded, setExpanded] = useState(false);
  
  
    return( 
    <header className="header">
<Navbar collapseOnSelect expand="lg" expanded={expanded}>
  <Container>
  <Navbar.Brand href="#home" className="animate__animated animate__heartBeat animate__repeat-3"><img src={Hari} alt="Logo" /></Navbar.Brand>
  <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={() => setExpanded(expanded ? false : "expanded")}><img src={Menu} alt="Logo"  /></Navbar.Toggle>
  <Navbar.Collapse id="responsive-navbar-nav">  
    <Nav>
   
    <Nav.Link>
      <Link to="About" activeClass="active" spy={true} offset={-400}  onClick={() => setExpanded(false)} smooth={true} duration={800}>
      About Me</Link>
    </Nav.Link> 
      
      <Nav.Link>
      <Link to="Skills" onClick={() => setExpanded(false)} smooth={true}  duration={800}>
        Skills</Link>
      </Nav.Link>
      
    
      <Nav.Link>
      <Link to="Projects" 
       ignoreCancelEvents={false}
       spyThrottle={500}  activeClass="active" hashSpy={true} isDynamic={true} spy={true}  offset={-200} onClick={() => setExpanded(false)} smooth={true}  duration={800}>
        Projects</Link>
      </Nav.Link>
      
    
      <Nav.Link >
      <Link to="Fresher"
      ignoreCancelEvents={false}
      spyThrottle={500}  activeClass="active" hashSpy={true} isDynamic={true} spy={true} offset={-200} onClick={() => setExpanded(false)}  smooth={true}  duration={800}>
        Fresher</Link>
        </Nav.Link>
     
      <Nav.Link >
      <Link to="Contact" activeClass="active" spy={true} offset={-50} onClick={() => setExpanded(false)} smooth={true}  duration={800}>
        Contact Me</Link>
        </Nav.Link>
     
      <Nav.Link  href="http://dhonimohanphotography.in/hari/index.html" className="header_rightButtons" target="_blank">Illuminati</Nav.Link>
      
    </Nav>
  </Navbar.Collapse>
  </Container>
</Navbar></header>
    )
  };
export default NavBoot;