import React from 'react';
import { Element } from 'react-scroll'; 
import skilimg from "./lapimg.jpg";
import  LinearProgress  from '@material-ui/core/LinearProgress';
import './skills.css';
import "animate.css";
import AOS from 'aos';
import 'aos/dist/aos.css';
import Lap from './laptop.png';

const Skill= ()=>{
    return(<div>
        <div id="Skills" className="scrool"></div>
    <Element className="skillContainer" >
        <div className="skillContainer_image" data-aos="fade-right"  
                                              data-aos-duration="2000"
                                              data-aos-offset="100">
            <img className="imgs" src={Lap} alt="imagesss"/>
        </div>
        <div className="skillContainer_text" data-aos="fade-left"
                                            data-aos-duration="1500" >
            <h2 className="animate__animated animate__backInLeft">SKILLSET</h2>
            <div className="skillContainer_skillset">
                <h5>React js</h5>
                <div className="skillContainer_slider skillContainer1">
                    <LinearProgress variant="determinate" value={72} />
                </div>
            </div>
            <div className="skillContainer_skillset">
                <h5>React Native</h5>
                <div className="skillContainer_slider skillContainer1">
                    <LinearProgress variant="determinate" value={22} />
                </div>
            </div>
            <div className="skillContainer_skillset">
                <h5>Java</h5>
                <div className="skillContainer_slider skillContainer2">
                    <LinearProgress variant="determinate" value={56} />
                </div>
            </div>
            <div className="skillContainer_skillset">
                <h5>Html,CSS</h5>
                <div className="skillContainer_slider skillContainer3">
                    <LinearProgress variant="determinate" value={79} />
                </div>
            </div>
            <div className="skillContainer_skillset">
                <h5>Js,Php</h5>
                <div className="skillContainer_slider skillContainer5">
                    <LinearProgress variant="determinate" value={42} />
                </div>
            </div>
        </div>

    </Element></div>
    )
}
AOS.init();
export default Skill;