import React from "react";
import { Link } from "react-scroll";
import "./TopContent.css";
import "animate.css";
const TopContent =() =>{
    return (
        <div className="topContent">
            <div className="toContent_container">
                <h1 className="animate__animated animate__rubberBand">
                    Mr.HAR<span>i</span>HAR<span>an</span>
                </h1>
                <p className="animate__animated animate__headShake">A Freasher Web And App Developer</p>
                <a href="https://drive.google.com/file/d/1LK6CUxUs30t95EW0vosxD5Ll0hDa0GKN/view?usp=sharing" target="_blank">
                    <button className="dwn_btn">Download Resume</button>
                </a>
                <Link to="Projects" smooth={true} duration={500}>
                    <button className="work_btn">My Work</button>
                </Link>
            </div>
        </div>
    );
};

export default TopContent;