import React from 'react';
import { Element } from 'react-scroll';
import './Freasher.css';
const Fresher =()=>{
    return(<div>
        <div  className="space"></div>
  
        <Element className="experienceContainer"  id="Fresher">
            <h1>Fresher</h1>
            <div className="ques" >
                <h5>Why fresher is important ?</h5>
                <p>Freshers are waiting for an opportunity to learn and grow. They are hardworking, driven and -
                     since they're fresh out of college 
                    - a lot more disciplined and receptive to training. This helps them learn quickly on the job.</p>
                    <p className="kk">
                    Freshers don’t come to you with skyrocketing salary expectations. They’re also full of ideas and eager to learn. For startups and companies with tight purse strings, freshers are the more economically sound choice.
                    </p>

            </div>

        </Element>  </div>
    )
}
export default Fresher;