import React from 'react';
import Project from '../projectjs/projectjs';
import { Element } from 'react-scroll';
import './Project.css';
import AOS from 'aos';
import 'aos/dist/aos.css';
const projectContainer =()=>{
    const projects =[
        {
            img:"https://images.unsplash.com/photo-1542038784456-1ea8e935640e?ixid=MnwxMjA3fDB8MHxzZWFyY2h8MXx8cGhvdG9ncmFwaHl8ZW58MHx8MHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60",
            title:"Photography Site",
            desc:"this site is madeup of Html,Css,Js,Php & Bootstrap ",
            link:"www.dhonimohanphotography.in",
        },
        {
            img:"https://wallpaperaccess.com/full/1540005.jpg",
            title:"Weather App",
            desc:"Utilized React Js with Mui frame Work",
            link:"https://hariharan666.github.io/Weather-App/",
        },
        {
            img:"https://img.freepik.com/free-vector/coming-soon-construction-illustration-design_1017-31446.jpg?size=626&ext=jpg",
            title:"Coming Soon",
        },
        
    ];
    return(<div>
        <div id="Projects"  className="scrooll"></div>
     <Element className="projectcontainer" >
         <h1 data-aos="fade-left">
             Projects
         </h1>
         <p data-aos="fade-left">
             Here are some Projects which I done making lives of people easy.
         </p>
         <div className="projectcontainer_projects" data-aos="zoom-in"
                                                    data-aos-duration="800">
             {
                 projects.map((project,index)=>
                 {
                     return(
                         <Project key={index} 
                         img={project.img} 
                         title={project.title} 
                         desc={project.desc} 
                         link={project.link} />
                     )
                 })

             }

         </div>
     </Element> 
      
</div>
    );
};
AOS.init();
export default projectContainer;